.skills_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.skills_container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}

.skills_container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.skills_content{
    display: grid;
    grid-template-columns: 1fr 1fr ;
    column-gap: 2rem;
    row-gap:2rem;
}

.skills_details{
    display: flex;
    gap: 1rem;
}

.skills_details_icon{
    margin-top: 6px;
    color: var(--color-primary);
}

@media screen and (max-width: 1024px) {
    .skills_container{
        grid-template-columns: 1fr;
    }
    .skills_container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .skills_content{
        padding: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .skills_container{
        gap: 1rem;
    }

    .skills_container > div{
        width:100%;
        padding: 2rem 1rem;
    }
}